
@charset "UTF-8";
* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.shopCart {
  position: fixed;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 10;
  height: 48px;
  background: #141d27;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.shopCart .content-left {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    /*border:1px solid orange;*/
}
.shopCart .content-left .logo-wrapper {
      -webkit-box-sizing: border-box;
              box-sizing: border-box;
      display: inline-block;
      /*inline-block经常和vertical-align配合使用*/
      vertical-align: top;
      position: relative;
      top: -10px;
      width: 56px;
      height: 56px;
      margin: 0 12px 0 12px;
      padding: 6px;
      border-radius: 50%;
      background: #141d27;
}
.shopCart .content-left .logo-wrapper .logo {
        /*box-sizing:content-box;*/
        position: absolute;
        width: 44px;
        height: 44px;
        border-radius: 50%;
        background: #2B343C;
        /*border:1px solid blue;*/
}
.shopCart .content-left .logo-wrapper .logo.highLight {
          background: #00a0dc;
}
.shopCart .content-left .logo-wrapper .logo .icon-shopping_cart {
          position: absolute;
          left: 50%;
          top: 50%;
          -webkit-transform: translate(-50%, -50%);
                  transform: translate(-50%, -50%);
          font-size: 24px;
          line-height: 24px;
          color: rgba(255, 255, 255, 0.4);
}
.shopCart .content-left .logo-wrapper .logo .icon-shopping_cart.highLight {
            color: white;
}
.shopCart .content-left .logo-wrapper .totalCount {
        position: absolute;
        top: 0;
        right: 0;
        width: 24px;
        height: 16px;
        line-height: 16px;
        text-align: center;
        font-size: 9px;
        font-weight: 700;
        color: white;
        border-radius: 8px;
        background: #f01414;
}
.shopCart .content-left .price {
      display: inline-block;
      vertical-align: top;
      /*可以把东西提到上面来，很好用，之前写到这里卡住了就是因为没想起来这个属性*/
      /*margin:0 12px 0 0;*/
      height: 100%;
      width: 37px;
      line-height: 48px;
      font-size: 16px;
      font-weight: 700;
      color: rgba(255, 255, 255, 0.4);
}
.shopCart .content-left .description {
      display: inline-block;
      position: relative;
      vertical-align: top;
      height: 36px;
      line-height: 36px;
      padding-left: 12px;
      margin: 6px auto;
      font-size: 16px;
      font-weight: 200;
      color: rgba(255, 255, 255, 0.4);
      /*border-left:1px solid rgba(255,255,255,0.1);*/
}
.shopCart .content-left .description:after {
        display: block;
        position: absolute;
        left: 0;
        top: 0;
        content: '';
        width: 0;
        height: 100%;
        border-left: 1px solid rgba(255, 255, 255, 0.1);
}
.shopCart .content-right {
    -ms-flex-preferred-size: 105px;
        flex-basis: 105px;
    width: 105px;
    height: 48px;
    line-height: 48px;
    text-align: center;
    /*border:1px solid red;*/
    font-size: 14px;
    font-weight: 300;
    color: rgba(255, 255, 255, 0.4);
    background: #2B343B;
}
.shopCart .content-right.pay {
      background: #00b43c;
      color: white;
}
.shopCart .ball-container .ball {
    position: fixed;
    left: 32px;
    bottom: 22px;
    z-index: 10;
    /*width:16px;*/
    /*height:16px;*/
    /*border-radius:50%;*/
    /*background:rgb(0,160,220);*/
    -webkit-transition: all 0.4s cubic-bezier(0.49, -0.29, 0.75, 0.41);
    transition: all 0.4s cubic-bezier(0.49, -0.29, 0.75, 0.41);
    /*&.drop-enter-active{*/
    /*transition: all 0.4 cubic-bezier(.17,-0.39,.96,.18);*/
    /*}*/
}
.shopCart .ball-container .ball .inner {
      width: 16px;
      height: 16px;
      border-radius: 50%;
      background: #00a0dc;
      -webkit-transition: all 0.4s;
      transition: all 0.4s;
}
.shopCart .shopcart-list {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 48px;
    background: rgba(7, 17, 27, 0.6);
    z-index: -1;
}
.shopCart .shopcart-list .shopcart-content {
      position: absolute;
      left: 0;
      right: 0;
      bottom: -100%;
}
.shopCart .shopcart-list .shopcart-content.show {
        bottom: 0;
}
.shopCart .shopcart-list .shopcart-content .title {
        height: 40px;
        line-height: 40px;
        padding: 0 18px;
        background: #f3f5f7;
        border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}
.shopCart .shopcart-list .shopcart-content .title:after {
          display: block;
          content: '';
          width: 100%;
          height: 0;
          visibility: hidden;
          clear: both;
}
.shopCart .shopcart-list .shopcart-content .title .cart {
          font-size: 14px;
          font-weight: 200;
          color: #07111b;
          float: left;
}
.shopCart .shopcart-list .shopcart-content .title .removeAll {
          font-size: 12px;
          font-weight: 200;
          color: #00a0dc;
          float: right;
}
.shopCart .shopcart-list .shopcart-content .food-list ul {
        background: #fff;
}
.shopCart .shopcart-list .shopcart-content .food-list ul .everyFood {
          position: relative;
          height: 48px;
          line-height: 48px;
          margin: 0 16px;
          border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}
.shopCart .shopcart-list .shopcart-content .food-list ul .everyFood .name {
            display: inline-block;
            width: 215px;
            font-size: 14px;
            color: #07111b;
            line-height: 48px;
}
.shopCart .shopcart-list .shopcart-content .food-list ul .everyFood .price {
            display: inline-block;
            line-height: 48px;
            margin-left: 15px;
            font-size: 14px;
            font-weight: 700;
            color: #f01414;
}
.shopCart .shopcart-list .shopcart-content .food-list ul .everyFood .control {
            position: absolute;
            top: 50%;
            right: -3px;
            height: 30px;
            -webkit-transform: translateY(-50%);
                    transform: translateY(-50%);
}



