
.rating-select .food-rating {
  margin: 18px 18px 0 18px;
  padding-bottom: 18px;
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}
.rating-select .food-rating .title {
    font-size: 14px;
    line-height: 14px;
    font-weight: 200;
    color: #07111b;
}
.rating-select .food-rating .block-wrapper {
    margin-top: 18px;
    font-size: 0;
}
.rating-select .food-rating .block-wrapper .block {
      display: inline-block;
      text-align: center;
      width: 80px;
      height: 45px;
      line-height: 45px;
      margin-right: 8px;
      border-radius: 2px;
      color: #4d555d;
      background: #ddd;
}
.rating-select .food-rating .block-wrapper .block.all {
        background: rgba(0, 160, 220, 0.2);
}
.rating-select .food-rating .block-wrapper .block.all.active {
          background: #00a0dc;
          color: #fff;
}
.rating-select .food-rating .block-wrapper .block.positive {
        background: rgba(0, 160, 220, 0.2);
}
.rating-select .food-rating .block-wrapper .block.positive.active {
          background: #00a0dc;
          color: #fff;
}
.rating-select .food-rating .block-wrapper .block.negative {
        background: rgba(77, 85, 93, 0.2);
}
.rating-select .food-rating .block-wrapper .block.negative.active {
          background: #4d555d;
          color: #fff;
}
.rating-select .food-rating .block-wrapper .block .text {
        font-size: 12px;
        margin-right: 4px;
}
.rating-select .food-rating .block-wrapper .block .number {
        font-size: 8px;
}
.rating-select .only-content {
  padding: 0 18px;
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
  font-size: 0;
}
.rating-select .only-content .icon-wrapper {
    line-height: 48px;
    display: inline-block;
    font-size: 24px;
    margin-right: 4px;
    color: #93999f;
}
.rating-select .only-content .icon-wrapper.active {
      color: #00a0dc;
}
.rating-select .only-content .text {
    vertical-align: top;
    line-height: 48px;
    display: inline-block;
    font-size: 12px;
    color: #93999f;
}
.rating-select .only-content .text.active {
      color: #00a0dc;
}



