
* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
.move-enter-active, .move-leave-active {
  -webkit-transition: all 0.4s;
  transition: all 0.4s;
}
.move-enter, .move-leave-to {
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.move-enter-to, .move-leave {
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.food {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 48px;
  background: #fff;
  z-index: 5;
  overflow: hidden;
}
.food .food-content {
    overflow-y: scroll;
    width: 102%;
    height: 100%;
}
.food .food-content .image-header {
      position: relative;
      -webkit-box-sizing: content-box;
              box-sizing: content-box;
      width: 100%;
      height: 0;
      padding-bottom: 100%;
      /*border:1px solid red;*/
}
.food .food-content .image-header img {
        position: absolute;
        top: 0;
        width: 100%;
        height: 100%;
}
.food .food-content .image-header .icon-wrapper {
        position: absolute;
        left: 15px;
        top: 20px;
}
.food .food-content .image-header .icon-wrapper .icon-arrow_lift {
          font-size: 24px;
          color: white;
}
.food .food-content .content {
      position: relative;
      padding: 18px;
}
.food .food-content .content .name {
        font-size: 14px;
        font-weight: 700;
        color: #07111b;
        line-height: 14px;
}
.food .food-content .content .description {
        margin-top: 8px;
        font-size: 0;
        color: #93999f;
}
.food .food-content .content .description .sellCount {
          margin-right: 12px;
          display: inline-block;
          font-size: 10px;
}
.food .food-content .content .description .rating {
          display: inline-block;
          font-size: 10px;
}
.food .food-content .content .price {
        margin-top: 16px;
        padding-bottom: 16px;
        font-size: 0;
}
.food .food-content .content .price .nowPrice {
          display: inline-block;
          line-height: 24px;
          margin-right: 8px;
          font-size: 14px;
          font-weight: normal;
          color: #f01414;
}
.food .food-content .content .price .oldPrice {
          display: inline-block;
          vertical-align: top;
          line-height: 24px;
          font-size: 10px;
          font-weight: normal;
          color: #93999f;
          text-decoration: line-through;
}
.food .food-content .content .addToCart {
        position: absolute;
        right: 18px;
        bottom: 0;
        width: 85px;
}
.food .food-content .content .addToCart .add {
          position: absolute;
          right: 0;
          bottom: 15px;
          vertical-align: top;
          text-align: center;
          width: 74px;
          height: 24px;
          /*line-height:10px;*/
          border-radius: 12px;
          font-size: 10px;
          color: white;
          background: #00a0dc;
          border: none;
          z-index: 1;
}
.food .food-content .content .addToCart .add:focus {
            outline: none;
}
.food .food-content .content .addToCart .add.button-enter-acitve, .food .food-content .content .addToCart .add.button-leave-active {
            -webkit-transition: all 1s;
            transition: all 1s;
}
.food .food-content .content .addToCart .add.button-enter, .food .food-content .content .addToCart .add.button-leave-to {
            opacity: 0;
}
.food .food-content .information {
      padding: 18px;
}
.food .food-content .information .title {
        line-height: 14px;
        font-size: 14px;
        font-weight: 300;
        color: #07111b;
}
.food .food-content .information .text {
        padding: 6px 8px 0 8px;
        font-size: 12px;
        font-weight: 200;
        color: #4d555d;
        line-height: 24px;
}
.food .food-content .ratings-wrapper ul .rating-item {
      position: relative;
      margin: 16px 18px 0;
      padding-bottom: 16px;
      border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}
.food .food-content .ratings-wrapper ul .rating-item .comment .comment-time {
        line-height: 12px;
        margin-bottom: 6px;
        font-size: 10px;
        color: #93999f;
}
.food .food-content .ratings-wrapper ul .rating-item .comment .comment-text {
        line-height: 16px;
        font-size: 12px;
        color: #07111b;
}
.food .food-content .ratings-wrapper ul .rating-item .user {
        position: absolute;
        right: 0;
        top: 0;
        font-size: 0;
}
.food .food-content .ratings-wrapper ul .rating-item .user .user-name {
          display: inline-block;
          vertical-align: top;
          margin-right: 6px;
          font-size: 10px;
          line-height: 12px;
          color: #93999f;
}
.food .food-content .ratings-wrapper ul .rating-item .user .avatar {
          display: inline-block;
          vertical-align: top;
          width: 12px;
          height: 12px;
          border-radius: 50%;
}
.food .food-content .ratings-wrapper ul .rating-item .user .avatar img {
            width: 100%;
            height: 100%;
            border-radius: 50%;
}



