
.seller {
  position: absolute;
  left: 0;
  top: 175px;
  bottom: 0;
  width: 100%;
  overflow: hidden;
}
.seller .content-wrapper {
    overflow-y: scroll;
    width: 102%;
    height: 100%;
}
.seller .content-wrapper .overview {
      margin: 18px;
}
.seller .content-wrapper .overview .basic {
        position: relative;
        padding-bottom: 18px;
        border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}
.seller .content-wrapper .overview .basic .name {
          line-height: 14px;
          font-size: 14px;
          color: #07111b;
          margin-bottom: 8px;
}
.seller .content-wrapper .overview .basic .extra {
          font-size: 0;
          height: 17px;
}
.seller .content-wrapper .overview .basic .extra .star {
            display: inline-block;
            /*vertical-align:top;*/
            margin-right: 8px;
            font-size: 10px;
}
.seller .content-wrapper .overview .basic .extra .star span {
              margin-right: 4px;
}
.seller .content-wrapper .overview .basic .extra .ratingCount {
            display: inline-block;
            vertical-align: top;
            line-height: 17px;
            margin-right: 12px;
            font-size: 10px;
}
.seller .content-wrapper .overview .basic .extra .sellCount {
            display: inline-block;
            vertical-align: top;
            line-height: 17px;
            font-size: 10px;
}
.seller .content-wrapper .overview .basic .collection {
          position: absolute;
          right: 0;
          top: 0;
          text-align: center;
          width: 36px;
}
.seller .content-wrapper .overview .basic .collection .iconWrapper {
            line-height: 24px;
            font-size: 24px;
            color: rgba(7, 17, 27, 0.5);
}
.seller .content-wrapper .overview .basic .collection .iconWrapper.active {
              color: #f01414;
}
.seller .content-wrapper .overview .basic .collection .collect {
            line-height: 10px;
            font-size: 10px;
            font-weight: 300;
            color: #4d555d;
}
.seller .content-wrapper .overview .basic .collection .collected {
            line-height: 10px;
            font-size: 10px;
            font-weight: 300;
            color: #4d555d;
}
.seller .content-wrapper .overview .moreInfo {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
}
.seller .content-wrapper .overview .moreInfo .send, .seller .content-wrapper .overview .moreInfo .deliveryMoney, .seller .content-wrapper .overview .moreInfo .deliveryTime {
          -webkit-box-flex: 1;
              -ms-flex: 1;
                  flex: 1;
          margin: 18px 0 0 0;
          text-align: center;
}
.seller .content-wrapper .overview .moreInfo .send .title, .seller .content-wrapper .overview .moreInfo .deliveryMoney .title, .seller .content-wrapper .overview .moreInfo .deliveryTime .title {
            font-size: 10px;
            line-height: 10px;
            color: rgba(7, 17, 27, 0.5);
}
.seller .content-wrapper .overview .moreInfo .send .price, .seller .content-wrapper .overview .moreInfo .deliveryMoney .price, .seller .content-wrapper .overview .moreInfo .deliveryTime .price {
            font-size: 0;
            margin-top: 4px;
}
.seller .content-wrapper .overview .moreInfo .send .price .number, .seller .content-wrapper .overview .moreInfo .deliveryMoney .price .number, .seller .content-wrapper .overview .moreInfo .deliveryTime .price .number {
              font-size: 24px;
              font-weight: 200;
              color: #07111b;
              line-height: 24px;
}
.seller .content-wrapper .overview .moreInfo .send .price .unit, .seller .content-wrapper .overview .moreInfo .deliveryMoney .price .unit, .seller .content-wrapper .overview .moreInfo .deliveryTime .price .unit {
              margin-top: 4px;
              font-size: 10px;
              font-weight: 200;
              color: #07111b;
              line-height: 24px;
}
.seller .content-wrapper .bulletin {
      margin: 18px 18px 0 18px;
}
.seller .content-wrapper .bulletin .title {
        line-height: 14px;
        font-size: 14px;
        color: #07111b;
}
.seller .content-wrapper .bulletin .content {
        padding: 8px 12px 16px 12px;
        line-height: 24px;
        font-size: 12px;
        font-weight: 200;
        color: #f01414;
}
.seller .content-wrapper .bulletin .wrapper ul .bulletin-item {
        padding-left: 12px;
        height: 48px;
        font-size: 0;
        border-top: 1px solid rgba(7, 17, 27, 0.1);
}
.seller .content-wrapper .bulletin .wrapper ul .bulletin-item .icon {
          display: inline-block;
          vertical-align: top;
          width: 16px;
          height: 16px;
          margin: 16px 6px 16px 0;
          background-size: 16px 16px;
          background-repeat: no-repeat;
}
.seller .content-wrapper .bulletin .wrapper ul .bulletin-item .icon.decrease {
            background-image: url(decrease_4@2x.png);
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-radio: 3) {
.seller .content-wrapper .bulletin .wrapper ul .bulletin-item .icon.decrease {
                background-image: url(decrease_4@3x.png);
}
}
.seller .content-wrapper .bulletin .wrapper ul .bulletin-item .icon.discount {
            background-image: url(discount_4@2x.png);
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-radio: 3) {
.seller .content-wrapper .bulletin .wrapper ul .bulletin-item .icon.discount {
                background-image: url(discount_4@3x.png);
}
}
.seller .content-wrapper .bulletin .wrapper ul .bulletin-item .icon.special {
            background-image: url(special_4@2x.png);
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-radio: 3) {
.seller .content-wrapper .bulletin .wrapper ul .bulletin-item .icon.special {
                background-image: url(special_4@3x.png);
}
}
.seller .content-wrapper .bulletin .wrapper ul .bulletin-item .icon.invoice {
            background-image: url(invoice_4@2x.png);
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-radio: 3) {
.seller .content-wrapper .bulletin .wrapper ul .bulletin-item .icon.invoice {
                background-image: url(invoice_4@3x.png);
}
}
.seller .content-wrapper .bulletin .wrapper ul .bulletin-item .icon.guarantee {
            background-image: url(guarantee_4@2x.png);
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-radio: 3) {
.seller .content-wrapper .bulletin .wrapper ul .bulletin-item .icon.guarantee {
                background-image: url(guarantee_4@3x.png);
}
}
.seller .content-wrapper .bulletin .wrapper ul .bulletin-item .information {
          display: inline-block;
          line-height: 48px;
          font-size: 12px;
}
.seller .content-wrapper .scene {
      width: 90%;
      padding: 18px;
}
@media screen and (max-width: 320px) {
.seller .content-wrapper .scene .scene {
          width: 85%;
}
}
.seller .content-wrapper .scene .title {
        line-height: 14px;
        margin-bottom: 12px;
        font-size: 14px;
        color: #07111b;
}
.seller .content-wrapper .scene .picture-wrapper {
        width: 100%;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
}
.seller .content-wrapper .scene .picture-wrapper .picture {
          -webkit-box-flex: 1;
              -ms-flex: 1;
                  flex: 1;
          /*display:inline-block;*/
          width: 20%;
          margin-right: 4px;
}
.seller .content-wrapper .scene .picture-wrapper .picture img {
            width: 100%;
            border-radius: 2px;
}
.seller .content-wrapper .informations {
      padding: 18px 18px 0 18px;
}
.seller .content-wrapper .informations .title {
        line-height: 14px;
        margin-bottom: 12px;
        font-size: 14px;
        color: #07111b;
}
.seller .content-wrapper .informations .info {
        line-height: 16px;
        padding: 16px 12px;
        font-size: 12px;
        font-weight: 200;
        color: #07111b;
        border-top: 1px solid rgba(7, 17, 27, 0.1);
}



