
.ratings {
  position: absolute;
  top: 175px;
  left: 0;
  bottom: 0;
  width: 100%;
  overflow: hidden;
}
.ratings .ratings-content {
    overflow-y: scroll;
    width: 102%;
    height: 100%;
}
.ratings .ratings-content .overview {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      margin: 18px 0;
}
.ratings .ratings-content .overview .overview-left {
        text-align: center;
        -ms-flex-preferred-size: 140px;
            flex-basis: 140px;
        padding: 12px 10px;
        border-right: 1px solid rgba(7, 17, 27, 0.2);
}
.ratings .ratings-content .overview .overview-left .score {
          font-size: 24px;
          color: #ff9900;
          line-height: 28px;
          margin-bottom: 6px;
}
.ratings .ratings-content .overview .overview-left .title {
          font-size: 12px;
          color: #07111b;
          line-height: 12px;
          margin-bottom: 8px;
}
.ratings .ratings-content .overview .overview-left .rank {
          font-size: 10px;
          color: rgba(7, 17, 27, 0.5);
          line-height: 10px;
}
.ratings .ratings-content .overview .overview-right {
        -webkit-box-flex: 1;
            -ms-flex: 1;
                flex: 1;
        padding: 12px 0 12px 18px;
}
.ratings .ratings-content .overview .overview-right .service-score, .ratings .ratings-content .overview .overview-right .food-score {
          font-size: 0;
          margin-bottom: 8px;
}
.ratings .ratings-content .overview .overview-right .service-score .title, .ratings .ratings-content .overview .overview-right .food-score .title {
            display: inline-block;
            line-height: 15px;
            font-size: 12px;
            margin-right: 10px;
}
.ratings .ratings-content .overview .overview-right .service-score .star, .ratings .ratings-content .overview .overview-right .food-score .star {
            display: inline-block;
            vertical-align: top;
}
.ratings .ratings-content .overview .overview-right .delivery-time .title {
          display: inline-block;
          line-height: 15px;
          font-size: 12px;
          margin-right: 10px;
}
.ratings .ratings-content .overview .overview-right .delivery-time .time {
          font-size: 12px;
          color: #93999f;
}
.ratings .ratings-content .ratings-wrapper ul .rating-item {
      position: relative;
      margin: 16px 18px 0;
      padding-bottom: 16px;
      border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}
.ratings .ratings-content .ratings-wrapper ul .rating-item .comment .comment-time {
        line-height: 12px;
        margin-bottom: 6px;
        font-size: 10px;
        color: #93999f;
}
.ratings .ratings-content .ratings-wrapper ul .rating-item .comment .comment-text {
        line-height: 16px;
        font-size: 12px;
        color: #07111b;
}
.ratings .ratings-content .ratings-wrapper ul .rating-item .user {
        position: absolute;
        right: 0;
        top: 0;
        font-size: 0;
}
.ratings .ratings-content .ratings-wrapper ul .rating-item .user .user-name {
          display: inline-block;
          vertical-align: top;
          margin-right: 6px;
          font-size: 10px;
          line-height: 12px;
          color: #93999f;
}
.ratings .ratings-content .ratings-wrapper ul .rating-item .user .avatar {
          display: inline-block;
          vertical-align: top;
          width: 12px;
          height: 12px;
          border-radius: 50%;
}
.ratings .ratings-content .ratings-wrapper ul .rating-item .user .avatar img {
            width: 100%;
            height: 100%;
            border-radius: 50%;
}



