
.cartControl {
  position: absolute;
  right: 0;
  bottom: 12px;
  font-size: 0;
}
.cartControl .cart-remove {
    display: inline-block;
    vertical-align: top;
    line-height: 24px;
    padding: 3px;
    font-size: 24px;
    color: #00a0dc;
}
.cartControl .cart-remove.move-enter-active, .cartControl .cart-remove.move-leave-active {
      -webkit-transition: all 0.4s linear;
      transition: all 0.4s linear;
}
.cartControl .cart-remove.move-enter, .cartControl .cart-remove.move-leave-to {
      opacity: 0;
      -webkit-transform: translate3D(24px, 0, 0) rotate(180deg);
              transform: translate3D(24px, 0, 0) rotate(180deg);
}
.cartControl .cart-count {
    display: inline-block;
    vertical-align: top;
    text-align: center;
    padding: 3px 0;
    height: 24px;
    width: 10px;
    line-height: 24px;
    font-size: 10px;
    color: #93999f;
}
.cartControl .cart-add {
    display: inline-block;
    vertical-align: top;
    padding: 3px;
    font-size: 24px;
    color: #00a0dc;
}



