
#app .nav {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  line-height: 40px;
  width: 100%;
  /*border-bottom:1px solid rgba(7,17,27,0.2);*/
  position: relative;
}
#app .nav:after {
    display: block;
    position: absolute;
    left: 0;
    bottom: 0;
    content: '';
    width: 100%;
    border-top: 1px solid rgba(7, 17, 27, 0.2);
}
#app .nav .tab-item {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    text-align: center;
}
#app .nav .tab-item a {
      font-size: 14px;
      color: #4d555d;
}
#app .nav .tab-item a.active {
      color: #f01414;
}



